body {
  width: 1440px;
  height: 800px;
  background-color: hsl(30, 38%, 92%);
}

main {
  display: flex;
  flex-direction: row;
}

article {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

section {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}